.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f8f9fa;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  transition: all 0.3s ease;
}

.section:hover,
.section:focus {
  transform: scale(1.05);
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.2);
  background-color: #fdfae7;
}

.h1 {
  font-size: 1.8em;
  color: #3778c8;
  margin-bottom: 15px;
  font-weight: bold;
  text-transform: uppercase;
}

.h2 {
  font-size: 1.5em;
  color: #3778c8;
  margin-top: 30px;
  margin-bottom: 15px;
  font-weight: bold;
}

.p {
  font-size: 1.1em;
  color: #555555; /* slightly darker text for paragraphs */
  line-height: 1.8em;
  text-align: justify;
}

.ul {
  list-style: none;
  padding: 0;
}

.ul li {
  padding: 15px 0;
  border-bottom: 1px solid #cccccc;
  font-weight: 500;
  color: #333333;
}

.ul li:last-child {
  border-bottom: none;
}

.speakersContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
